body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Repository-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.Stats-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 50px;
  margin: 0 20px;
}

.Stats-star {
  color: #ffd700;
}

.Stats-issue {
  color: grey;
}

.Labels-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Description-container > .Labels-container {
  -webkit-flex-direction: row;
          flex-direction: row;
}

.Description-container .Label-container {
  margin-right: 10px;
}

.Label-container {
  margin: 4px 0;
}

.Label {
  background-color: grey;
  border-radius: 2px;
  box-shadow: inset 0 -1px 0 rgba(27, 31, 35, 0.12);
  font-size: 13px;
  font-weight: 600;
  height: 20px;
  line-height: 16px;
  padding: 0.15em 4px;
  margin: 3px 0;
}

.Description-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Description-link {
  font-size: 1.2rem;
  font-weight: 700;
}

.Description-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 460px;
  overflow: hidden;
}

@media screen and (min-width: 1000px) {
  .Repository-container {
    min-width: 650px;
  }
}

@media screen and (max-width: 999px) {
  .Repository-container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .Repository-header {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-bottom: 10px;
  }

  .ui.tiny.image {
    height: 80px;
  }

  .Description-container {
    width: 100%;
  }

  .Description-text {
    white-space: normal;
  }

  .Stats-container {
    -webkit-align-self: center;
            align-self: center;
    margin-left: 15px;
    margin-right: 10px;
  }

  .Labels-container {
    margin-top: 5px;
  }

  .Label-container {
    margin: 5px 0;
  }
}

.App-container {
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
}

.App-controls-container {
  position: relative;
  height: 100%;
  width: 300px;
}

.App-sidebar-left {
  left: 0;
  -webkit-clip-path: polygon(0 0, 100% 0, 90% 100%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 90% 100%, 0 100%);
  -webkit-animation-name: polygons-left;
          animation-name: polygons-left;
}

.App-sidebar-right {
  right: 0;
  -webkit-clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
          clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
  -webkit-animation-name: polygons-right;
          animation-name: polygons-right;
}

@-webkit-keyframes polygons-left {
  100% {
    -webkit-clip-path: polygon(0% 0, 90% 0, 100% 100%, 0 100%);
            clip-path: polygon(0% 0, 90% 0, 100% 100%, 0 100%);
  }
}

@keyframes polygons-left {
  100% {
    -webkit-clip-path: polygon(0% 0, 90% 0, 100% 100%, 0 100%);
            clip-path: polygon(0% 0, 90% 0, 100% 100%, 0 100%);
  }
}

@-webkit-keyframes polygons-right {
  100% {
    -webkit-clip-path: polygon(0% 0, 100% 0, 100% 100%, 10% 100%);
            clip-path: polygon(0% 0, 100% 0, 100% 100%, 10% 100%);
  }
}

@keyframes polygons-right {
  100% {
    -webkit-clip-path: polygon(0% 0, 100% 0, 100% 100%, 10% 100%);
            clip-path: polygon(0% 0, 100% 0, 100% 100%, 10% 100%);
  }
}

.App-container.out > .App-sidebar-left {
  left: -300px;
  -webkit-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
}

.App-container.out > .App-sidebar-right {
  right: -300px;
  -webkit-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
}

.App-container.in > .App-sidebar-left {
  left: 0;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.App-container.in > .App-sidebar-right {
  right: 0;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.App-main {
  background: white;
  padding: 10px;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 1000px) {
  .App-container {
    width: 1000px;
  }

  .App-main {
    margin-left: 340px;
    width: 700px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .ui.segment.App-controls {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: calc(100vh - 20px);
    position: fixed;
    margin: 10px;
    width: 300px;
  }
}

.ui.label.App-github {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-decoration: none;
  background-color: #f1f8ff;
  color: #0366d6;
  font-size: 14px;
  line-height: 17px;
  margin-top: 10px;
  width: 100%;
  text-align: center;
}

.ui.label.App-github:hover {
  background-color: #def;
  color: #0366d6;
}

i.icon.App-github-icon {
  font-size: 17px;
}

.LanguageDescription-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  min-height: 0;
}

.LanguageDescription-container > * {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.LanguageDescription-image {
  width: 100%;
  max-height: 300px;
  margin: 10px 0;
}

.LanguageDescription-text {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  overflow: auto;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  min-height: 0;
}

.App-dropdown.ui.search.dropdown .menu {
  max-height: 16.02857143rem;
}

.App-social {
  display: -webkit-flex;
  display: flex;
  margin-top: 10px;
}

.App-social > .ui.basic.button {
  padding: 10px 1.5em 10px;
  height: 32px;
}

.App-mobile-header {
  position: fixed !important;
  background-color: white;
  top: 0;
  width: 100%;
  -webkit-backface-visibility: hidden;
  z-index: 100;
}

@media screen and (max-width: 999px) {
  .App-container {
  }

  .App-main {
    margin-top: 100px;
  }

  .ui.dropdown.App-dropdown {
    margin: 10px;
    width: calc(100% - 20px);
  }

  input {
    font-size: 16px;
  }

  .ui.label.App-github {
    margin: 10px;
    width: calc(100% - 20px);
  }
}

